@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
.p-arrow {
  display: none;
}
.p-arrow.active {
  display: block;
  cursor: pointer;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.p-arrow p {
  line-height: 1.5;
  color: #c73020;
  text-align: left;
  margin: -20px 0;
  width: 70px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  -webkit-animation: r-arrow .4s ease forwards;
          animation: r-arrow .4s ease forwards;
}
@-webkit-keyframes r-arrow{
0% {-webkit-transform: translateY(0px);transform: translateY(0px); -webkit-transform: rotate(45deg); transform: rotate(45deg);
}
100% {-webkit-transform: translateY(-10px);transform: translateY(-10px); -webkit-transform: rotate(-45deg); transform: rotate(-45deg);
}
}
@keyframes r-arrow{
0% {-webkit-transform: translateY(0px);transform: translateY(0px); -webkit-transform: rotate(45deg); transform: rotate(45deg);
}
100% {-webkit-transform: translateY(-10px);transform: translateY(-10px); -webkit-transform: rotate(-45deg); transform: rotate(-45deg);
}
}
/*
* ヘッダー
*/
#header {
  width: 100%;
  height: 50px;
  position: fixed;
}

#header-nav {
  margin: 0;
  text-align: right;
  background-color: #000;
  opacity: .5;
}

#header-nav .menu{
  margin: 10px 20px;
}

#header-nav .menu .link{
  margin: 0 10px;
  color: #cbcec4;
}

#header-nav .menu .link:hover{
  border-bottom: 3px solid #cbcec4;
  padding-bottom: 5px;
}

.animated {
  /* 2秒遅れてアニメする */
  animation-delay: 1s;
  /* 3回繰り返す infiniteを指定すると無限ループ*/
  animation-iteration-count: 1;
}

/*
* mainページ
*/

#main {
  background-image: url('https://miiicha15146.github.io/portfolio/image/main.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  text-align: center;
}

.mainText {
  padding-top: 15%;
}

#title {
  font-size: 5em;
}

.vue-typer .custom.caret {
  width: 10px;
  background-color: #3F51B5;
}

#navigation {
  list-style: none;
  display: inline-block;
  margin: 50px 0 0;
  padding: 0;
  height: 70px;
}

#navigation .menu .link {
  margin: 10px 30px;
  color: #c73020;
  opacity: .7;
  font-size: 2rem;
}

#navigation .menu .link:hover {
  cursor: pointer;
  animation: menuLink .4s ease forwards;
}

@keyframes menuLink{
  0% {color: #cbcec4;}
  100% {color: #c73020;}
}

.arrow{
  margin: 30px auto 0;
  width: 30px;
  height: 30px;
  border: 5px solid;
  border-color:  transparent transparent #c73020 #c73020;
  opacity: .5;
  transform: rotate(-45deg);
}

.arrow:hover{
  cursor: pointer;
  animation: arrow .4s ease forwards;
}

@keyframes arrow{
  0% {border-color:  transparent transparent #cbcec4 #cbcec4;}
  100% {border-color:  transparent transparent #c73020 #c73020;}
}

/*
* aboutページ
*/
#about {
  width: 100%;
  height: 100%;
  top: 0;
  padding: 100px 0;
}

#about .content-wrap {
  width: 90%;
  margin: 0 auto;
}

#about .content-wrap .content {
  width: 60%;
  height: 500px;
  margin: 0 auto;
  padding: 30px 0 40px;
  text-align: center;
  border: 2px solid #cbcec4;
  box-shadow: 10px 10px 10px rgba(0,0,0,0.4);
}

#about .content-wrap .content p {
  line-height: 2;
}

/*
* プロフィルページ
*/
#profile {
  width: 100%;
  height: 100%;
  top: 0;
  padding: 100px 0;
}

#profile .content-wrap {
  width: 90%;
  margin: 0 auto;
}

#profile .content-wrap .content {
  width: 85%;
  height: 500px;
  margin: 0 auto;
  padding: 50px 0 0;
  text-align: center;
  border: 2px solid #cbcec4;
  box-shadow: 10px 10px 10px rgba(0,0,0,0.4);
}

#profile .content-wrap .content .data {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

#profile .content-wrap .content .data .myPhoto {
  width: 45%;
}

#profile .content-wrap .content .data .myPhoto img {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  margin-bottom: 30px;
}

#profile .content-wrap .content .data .myPhoto .contact-us {
  display: block;
  width: 200px;
  height: 20px;
  padding: 10px;
  border: 2px solid #cbcec4;
  margin: 0 auto;
  line-height: 1;
  color: #000;
}

#profile .content-wrap .content .data ul {
  width: 45%;
  text-align: left;
  margin: 20px 0;
}

#profile .content-wrap .content .data li {
  display: block;
  margin-bottom: 15px;
  font-size: 1.2em;
}

#profile .content-wrap .content .data li span {
  margin-right: 10px;
}

#profile .content-wrap .content .p-arrow {
  margin: 0 auto;
  width: 30px;
  height: 30px;
  border: 5px solid;
  border-color:  transparent transparent #c73020 #c73020;
  opacity: .5;
  transform: rotate(-45deg);
}

/*
* 経歴書
*/
#resume {
  width: 100%;
  height: 100%;
  top: 0;
  padding: 100px 0;
}

#resume .content-wrap {
  width: 90%;
  margin: 0 auto;
}

#resume .content-wrap .r-arrow {
  margin: -15px auto 0;
  width: 30px;
  height: 30px;
  border: 5px solid;
  border-color:  transparent transparent #c73020 #c73020;
  opacity: .5;
  transform: rotate(135deg);
}

#resume .content-wrap .r-arrow p {
  line-height: 1.5;
  color: #c73020;
  width: 30px;
  text-align: center;
  transform: rotate(-45deg);
}

#resume .content-wrap .content {
  width: 85%;
  height: 500px;
  margin: 0 auto;
  padding: 50px 0 0;
  text-align: center;
  border: 2px solid #cbcec4;
  box-shadow: 10px 10px 10px rgba(0,0,0,0.4);
}

#resume .content-wrap .content table {
  width: 85%;
  margin: 0 auto;
  border: 1px solid #cbcec4;
  border-collapse: collapse;
}


